import * as React from 'react';
import styles from './Warren.module.scss';
import type { IWarrenProps } from './IWarrenProps';
import { escape } from '@microsoft/sp-lodash-subset';

export default class Warren extends React.Component<IWarrenProps, {}> {
  public render(): React.ReactElement<IWarrenProps> {
    const {
      description,
      isDarkTheme,
      environmentMessage,
      hasTeamsContext,
      userDisplayName
    } = this.props;

    return (
      <section className={`${styles.warren} ${hasTeamsContext ? styles.teams : ''}`}>
        <div className={styles.welcome}>
          <img alt="" src={isDarkTheme ? require('../assets/welcome-dark.png') : require('../assets/welcome-light.png')} className={styles.welcomeImage} />
          <h2>Well done, {escape(userDisplayName)}!</h2>
          <div>{environmentMessage}</div>
          <div>Web part property value: <strong>{escape(description)}</strong></div>
        </div>
        <div>
          
        </div>
      </section>
    );
  }
}
